<template>
  <div class="home"> 
    <section class="banner">
      <mt-swipe :auto="4000">
        <mt-swipe-item>1</mt-swipe-item>
        <mt-swipe-item>2</mt-swipe-item>
        <mt-swipe-item>3</mt-swipe-item>
      </mt-swipe>
    </section>
    <section class="shops">
    <article v-for="(i,index) in all" :key="i.id" @click="push(index)">
      {{i.name}}
    </article>
    </section>
  </div>
</template>

<script>
import { Swipe, SwipeItem } from 'mint-ui';
export default {
  name: 'home',
  data () {
    return {
      all:[
        {name:"手机",num:1,price:"100",der:"手机"},
        {name:"男上衣",num:1,price:"100",der:"男上衣"},
        {name:"毛衫",num:1,price:"100",der:"毛衫"},
        {name:"电脑",num:1,price:"100",der:"电脑"},
        {name:"衣柜",num:1,price:"100",der:"衣柜"},
        {name:"女装",num:1,price:"100",der:"女装"},
        {name:"笔记本",num:1,price:"100",der:"笔记本"},
        {name:"手机",num:1,price:"100",der:"手机"},
        {name:"手机",num:1,price:"100",der:"手机"},
        {name:"手机",num:1,price:"100",der:"手机"}
      ]
    }
  },
  methods:{
    push(i){
      this.$store.commit('push',this.all[i])
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.banner{
  height:300px;
  background:#ccc;
}
.shops{
  width:100%;
  display:flex;
  flex-wrap:wrap;
  margin-bottom:50px;
}
.shops article{
  width:49%;
  margin-right:1%;
  margin-bottom:1%;
  height:100px;;
  background:#f00;
  font-size:25px;
  line-height: 100px;
}
</style>



