<template>
  <div class="scar">
    <ul>
      <li v-for="(item,index) in shops" :key="item.id">
          <span>{{item.name}}</span>
          <span>价格:</span><span>{{item.price}}</span>
          <input type="button" value="-" @click="del(index)">
          <input type="text" v-model="item.num" >
          <input type="button" value="+" @click="add(index)">
          <span @click="deletes(index)">删除</span>
      </li>
    </ul>
    <div class="total">
      <span>总价：</span>{{count}}
    </div>
  </div>
</template>

<script>
// 在单独构建的版本中辅助函数为 Vuex.mapState
import { mapState } from 'vuex'

export default {
  data () {
    return {
      
    }
  },
  computed:mapState({
    count () {
      return this.$store.state.count
    },
    shops(){
      this.$store.commit('increment')
      return this.$store.state.shops
    }

  }),
  methods:{
    del(i){
      this.$store.commit('del',i)
      this.$store.commit('increment')
    },
    add(i){
      this.$store.commit('add',i)//调用state里面的方法
      this.$store.commit('increment')
    },
    deletes(i){
      this.$store.commit('deletes',i);
      this.$store.commit('increment')
    }
  }   
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .scar{
    font-size:18px;
  }
  .scar input{
    width:20px;
    height:22px;
    border:1px solid #ccc;
    text-align: center;
  }
  .scar span{
    display:inline-block;
    width:60px;
  }
  .total{
    width:100%;
    height:30px;
    background:#f00;
    position:fixed;
    bottom:50px;
    text-align:right;
  }
  .scar ul{
    margin-bottom:80px;
  }
  .scar li{
   height:60px;
   border:1px dashed #ccc;
   line-height:60px;
  }
</style>



