<template>
  <div class="mine">
    <article class="form">
      <span>账户 :</span><input type="text" v-model="user"  @focus="getFocus(0)" @blur="loseFocus(0)" placeholder="请输入您的账号" >
      <br>
      <span>密码 :</span><input type="password" v-model="psw" @focus="getFocus(1)" @blur="loseFocus(1)" placeholder="请输入您的密码">
      <br>
      <input type="button" @click="login()" value="登入"> <input type="button" @click="register()" value="注册">
    </article>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      user:'',
      psw:""
    }
  },
  methods:{
    getFocus(dom){ 
      $(".mine input[type='text']").eq(dom).css({"border-color":"#000"})
    },
    loseFocus(dom){
      $(".mine input[type='text']").eq(dom).css({"border-color":"#ccc"})
    },
    login(){
      console.log("login")
      this.http({
        url:"http://localhost/login",
        method:'post',
        data:{
          user:this.user,
          psw:this.psw
        }
      }).then(
        res=>{
          console.log(res.data)
          alert(res.data)
        }
      )
    },
    register(){
      var that=this;
      console.log("register")
      this.http({
          method:"post",
          url:"http://localhost:80/reg",
          data:{
              user:this.user,
              psw:this.psw
          }
      }).then((res)=>{
          console.log(res.data);
          alert(res.data)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .mine{
    padding-top:20%;
  }
  .form{
    width:80%;
    height:60%;
   
    margin:0 auto;
  }
  .form span{
    font-size:20px;
    margin-left:10%;
    height:20px;
    line-height:20px;
    margin-right:2%;
  }
  .form input[type=text],input[type=password]{
    width:60%;
    height:22px;
    border:1px solid #ccc;
  }
  .form input[type=button]{
    width:50px;
    height:28px;
    border:1px solid #ccc;
    border-radius:3px;
  }
</style>



