<template>
  <div class="list">
    <aside class="left">
      <ul>
         <li v-for="i in list" :key="i.id">
            <router-link :to="{'name':i.path}">{{i.name}}</router-link>        
        </li>
      </ul>
    </aside>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list:[
        {name:"男装",path:"boy"},
        {name:"女装",path:"girl"},
        {name:"手机",path:"phone"},
        {name:"婴幼儿",path:"baby"},
        {name:"电脑",path:"com"},
        {name:"家居",path:"jj"},
        {name:"服装",path:"clo"},
        {name:"男装",path:"boy"},
        {name:"男装",path:"boy"},
        {name:"男装",path:"boy"}
      ],
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .left{
    width:35%;
    float:left;
  }
  .left li{
    width:100%;
    height:40px;
    line-height:40px;
    font-size:20px;
    text-align:center;
    margin-bottom:1px;
    background:#ccc;
  }

</style>



