import Vue from 'vue'
import Router from 'vue-router'
import index from "@/components/index"
import home from '@/components/home'
import list from "@/components/list"
import scar from "@/components/scar"
import mine from "@/components/mine"

import boy from "@/components/boy"
import girl from "@/components/girl"
import phone from "@/components/phone"
import baby from "@/components/baby"
import com from "@/components/com"
import jj from "@/components/jj"
import clo from "@/components/clo"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      component: index,
      redirect:"/home",
      children:[
        {
          path: 'home',
          name: 'home',
          component: home,
        },
        {
          path: 'list',
          name: 'list',
          component: list,
          redirect:"/list/boy",
          children:[
            {
              path:"boy",
              name:"boy",
              component:boy
            },
            {
              path:"girl",
              name:"girl",
              component:girl
            },
            {
              path:"phone",
              name:"phone",
              component:phone
            },
            {
              path:"baby",
              name:"baby",
              component:baby
            },
            {
              path:"com",
              name:"com",
              component:com
            },
            {
              path:"jj",
              name:"jj",
              component:jj
            },
            {
              path:"clo",
              name:"clo",
              component:clo
            }

          ]
        },
        {
          path: 'scar',
          name: 'scar',
          component: scar
        },
        {
          path: 'mine',
          name: 'mine',
          component: mine
        }
      ]
    } 
  ]
})



