import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export default new Vuex.Store({
	state:{//数据
		count:0,
		shops:[
			// {name:"手机",num:1,price:"100",der:"手机"},
			// {name:"男上衣",num:1,price:"100",der:"男上衣"},
			// {name:"毛衫",num:1,price:"100",der:"毛衫"},
			// {name:"电脑",num:1,price:"100",der:"电脑"},
			// {name:"衣柜",num:1,price:"100",der:"衣柜"},
			// {name:"女装",num:1,price:"100",der:"女装"},
			// {name:"笔记本",num:1,price:"100",der:"笔记本"},
		]
	},
	mutations:{//更改vuex状态的唯一方法
		increment(state){
			state.count=0;
			for(let i=0;i<state.shops.length;i++){
				state.count+=state.shops[i].num*state.shops[i].price;
			}
		},
		add(state,i){
			state.shops[i].num+=1
		},
		del(state,i){
			let num=state.shops[i].num;
			num=num<=0?0:num-1
			state.shops[i].num=num
		},
		push(state,obj){
			//遍历数组看看是否已经存在
			for(let i=0;i<state.shops.length;i++){
				if(obj.name==state.shops[i].name){
					state.shops[i].num+=obj.num;
					return false;
				}
			}
			state.shops.push(obj)
		},
		deletes(state,i){
			state.shops.splice(i,1)
		}
	},
	getters:{

	},
})


