<template>
  <footer class="footer">
    <ul class="footer">
        <li v-for="i in link" :key="i.id">
          <router-link :to="{'name':i.path}">{{i.name}}</router-link>
        </li>
    </ul>
  </footer>
</template>

<script>
export default {
  data () {
    return {
      link:[
        {path:"home",name:"首页"},
        {path:"list",name:"分类"},
        {path:"scar",name:"购物车"},
        {path:"mine",name:"我的"},
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.footer{
  position:fixed;
  bottom:0;
  width:100%;
  height:50px;
  border-top:1px solid #ccc;
  z-index:999;
  background:#00f;
}
.footer li{
  float:left;
  width:25%;
  height:100%;
  font-size:18px;
  text-align:center;
  margin-bottom:1px;
}
</style>



